
.game-board {
    margin: 25px;   
    width: 250px;
    height: 250px;
    display: grid;  
    grid-gap: 10px;
    margin-left: 40px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
}

.game-cell {
    cursor: pointer;
    display: flex;
    border-radius: 5px;
    border: 1px solid rgba(56, 56, 56, 0.2);
    justify-content: center;
    align-items: center;
    font-size: 40px;
    font-weight: bold;
    box-shadow: rgba(93, 96, 99, 0.2) 0px 8px 24px;
}
.game-reset {
    margin: 20px;
    display: inline-block;
    padding: 10px 20px;
    background-color: rgb(223, 79, 79);
    cursor: pointer;
    color: antiquewhite;
    font-size: 16px;
    border: none;
    border-radius: 5px;
}
.game-cell.is-x {
    color: rgba(255, 255, 255, 0.827);
    background-color: blue;
}
.game-cell.is-o {
    color: rgba(255, 0, 0, 0.827);
}.game-winner {
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    background-color: rgb(0, 112, 198);
    margin-top: 20px;
}
.your-turn {
    font-size: 30px;
    margin: 15px;
    font-weight: 600;
}

.your-draw {
    font-size: 30px;
    margin: 15px;
    font-weight: 600;
    color: crimson;
}